import { _decorator, Component, Node } from "cc";
import { Piece } from "../Piece/Piece";
import { EffectManager } from "../Effects/EffectManager";
import { GridManager } from "../Grid/GridManager";
const { ccclass, property } = _decorator;

@ccclass("SliderManager")
export class SliderManager extends Component {
  public Slide(pieceA: Piece, pieceB: Piece) {
    const posA = pieceA.node.getPosition();
    const posB = pieceB.node.getPosition();

    EffectManager.movePiece(pieceA.node, posB, 0.1);
    EffectManager.movePiece(pieceB.node, posA, 0.1);

    GridManager.getInstance().SwapPieces(pieceA,pieceB);

  }
}
